import { singletonMgr } from "./SingletonMgr";
export class Singleton {
  static getInstance() {
    if (!this.instance) {
      this.instance = new this();
      singletonMgr.sign(this);
    }

    return this.instance;
  }

  static destoryInstance() {
    if (this.instance) {
      this.instance.clear();
      this.instance = null;
      singletonMgr.unSign(this);
    }
  }

}

