  connect(ip, port, openFunc, errorFunc) {
    this._ip = ip;
    this._port = port;

    try {
      let url = `ws://${ip}:${port}`;
      let ws = new WebSocket(url);
      this._ws = ws;

      ws.onopen = event => {
        this._onopen(event);

        openFunc(event);
      };

      ws.onmessage = event => {
        this._onmessage(event);
      };

      ws.onerror = event => {
        this._onerror(event);

        errorFunc(event);
      };

      ws.onclose = event => {
        this._onclose(event);

        errorFunc(event);
      };
    } catch (error) {
      log("connect error: ", error);
    }
  }

  reConnect() {
    if (this._ip && this._port) {
      this.connect(this._ip, this._port, event => {}, event => {
        log(event);
      });
    }
  }

  close() {
    if (this._ws) {
      this._ws.close(4888);

      this._ws = null;
    }
  }

  send(msgId, data = {}) {
    data["proto"] = msgId;
    data = JSON.stringify(data);
    log("[WS] Send:", msgId, data);

    this._ws.send(data);

    netLoadingMgr.addMsgLoading(msgId);
  }

  sendInnerMsg(msgId, data = {}) {
    let msg = new Message(msgId, data);
    log("[WS] Send Inner:", msgId, data);
    gameMgr.addInnerMessage(msg);
  }

