import { instantiate, tween } from "cc";
import { ResourcesLoader } from "../../../../framework/data/ResourcesLoader";
;
import { BloodEffect, EffectBase } from "../effect/Effect";
import { FightEvent } from "../event/FightEvent";
import { fightEventMgr } from "../event/FightEventMgr";
import { FightConstant } from "../FightConstant";
import { ActionBase } from "./Action";
export class ActionAddPrefab extends ActionBase {
  /**
   * 
   * @param own 移动节点
   * @param duration 持续时间
   */
  add(data) {
    let animCfg = data.animCfg;
    let url = animCfg.params[0];
    return tween().call(() => {
      ResourcesLoader.load(url, prefab => {
        let node = instantiate(prefab);

        this._customSetting(data, node);
      });
    });
  }

  _customSetting(data, node, startCallback, endcallback) {
    let animCfg = data.animCfg;
    let name = animCfg.layer;
    let tar = data.target;
    let com = null;

    switch (name) {
      case "BLOOD":
        let args = {
          PrefabNode: node,
          Data: data
        };
        com = node.getComponent(BloodEffect);
        fightEventMgr.send(new FightEvent(FightConstant.FightEvent.Blood_Change, args));
        break;

      case "ROLE":
        com = node.getComponent(EffectBase);
        tar.addEffectFont(node);
        break;
    }

    com.setStartCallback(() => {
      if (startCallback) {
        startCallback();
      }
    });
    com.setEndCallback(() => {
      if (endcallback) {
        endcallback();
      }

      node.removeFromParent();
      node.destroy();
      node = null;
    });
  }

}

_cclegacy._RF.pop();